@import url("https://fonts.googleapis.com/css?family=Montserrat:regular,500,600,700,800,900");
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family:Montserrat,sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #02024b;
  padding: 5rem 1rem;
  color:#FBF4E2;
  display: flex;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root{
 width: 100%;
}
.app {
  text-align: center;
  display: grid;
  gap: 2rem;
  max-width: 900px;
  width: 100%;
  margin: 0 auto;
}
.app > h1{
  text-transform: uppercase;
  letter-spacing: 2px;
}
.todo-wrapper{
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #222281;
  padding: 1rem 1rem;
  border-radius: 5px;
  box-shadow: 0px 5px 7px #050514;
  width: 100%;
  max-width: 1000%;
}
.todo-input{
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  gap: 2rem;
  padding: 1rem 0.5rem;
  border-bottom:2px solid #8484e1;
}
.todo-input-item{
  display: flex;
  flex-direction: column;
  text-align: left;
  gap: 0.5rem;
  width: 100%;
}
.todo-input-item.modification{
  width: max-content;
}
.todo-input-item label{
  font-weight: 600;
  line-height: 1.2;
  font-size: 1.1rem;
  color:#008ae6;
}
.todo-input-item input{
  background-color: transparent;
  border: none;
  padding: 0.2rem 0.5rem 0.3rem 0.2rem;
  font-size: 0.9rem;
  font-weight: 500;
  font-family: Montserrat;
  color:#fff;
  outline: none;
  width: 100%;
  max-width: 250px;
  border-bottom: 2px solid #8484e1;
}
.todo-input-item input::placeholder{
  font-size: 0.8rem;
  color:#b3e1ffdc;
}
button{
  cursor: pointer;
  font-family: Montserrat;
  white-space: nowrap;
  user-select: none;

}
.todo-input-item .main-btn{
  font-weight: 600;
  line-height: 1.2;
  font-size: 1.1rem;
  color:#c3f7d5;
  background-color: #1aa3ff;
  border: none;
  padding: 0.7rem 2.5rem;
  border-radius: 5px;
  max-width: max-content;
  margin-left: auto;
  transition: all 0.2s;
}
.todo-input-item .main-btn:hover{
  background-color: #1aa3ffdd;
}
.todo-input-item .main-btn:disabled{
  opacity: 0.5;
  pointer-events: none;
}
.btn-area{
  width: 100%;
  text-align: left;
  padding: 4rem 0.3rem 0.5rem 0.3rem;
}
.btn-area-items{
  display: inline-flex;
  gap: 2px;
  background-color: #9f989b;
  border: 5px;
}
.btn-area-items .tabs-btn{
  padding: 0.5rem 2.3rem;
  border: none;
  color:#fff;
  font-weight: 700;
  background-color: #575455;
  opacity: 0.5;
  transition: all 0.2s;
  position: relative;
}
.btn-area-items .tabs-btn span{
  position: absolute;
  left: 50%;
  top: -12px;
  transform: translateX(-50%  );
  background-color: rgb(225, 225, 8);
  border-radius: 50%;
  font-size: 11px;
  width: 17px;
  height: 17px;
  display: flex;
  align-items: center;  
  justify-content: center;
  color: black;
  padding:1px 2px;
  text-align: center;
}
.btn-area-items .tabs-btn.active{
  background-color: #038ae4;
  opacity: 1;
}
.todo-list{
  display: grid;
  gap: 1rem;
  width: 100%;
  text-align: left;
  padding: 1rem 0;
}
.todo-list-item{
  background-color: #082a90e1;
  box-shadow: 0px 3px 10px #ffffff2b;
  padding: 1.3rem 1.7rem;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 3rem;
  width: 100%;
}
.todo-list-item h3{
  font-size: 27px;
  margin-bottom: 0.4rem;
  color: #4ab3fa;
  gap: .7rem;
  display: flex;
  align-items: center;
}
.todo-list-item h3 input{
  font-size: 20px;
  padding-bottom: 0.2rem;
  margin-bottom: 0.2rem;
  color: #4ab3fa;
  border: none;
  background-color: transparent;
  outline: none;
  border-bottom: 2px solid hsl(240, 61%, 70%); 
  font-family: Montserrat;
  font-weight: 600;
}
.todo-list-item p{
  font-size: 14px;
  color: rgb(198, 191, 191);
  display: flex;
  align-items: center;
  gap: .7rem;
}
.todo-list-item p input{
  font-size: 13px;
  color: rgb(198, 191, 191);
  border: none;
  background-color: transparent;
  outline: none;
  border-bottom: 1px solid hsl(240, 61%, 70%); 
  font-family: Montserrat;
  font-weight: 500;
  width: 100%;
}
.todo-list-item .rename-icon{
  cursor: pointer;
  opacity: 0.7;
  transition: all 0.3s;
}
.todo-list-item .rename-icon:hover{
  opacity: 1;
}
.todo-list-item p:not(:last-child){
  margin-bottom: 1rem;
}
.icons{
  display: flex;
  align-items: center;
  gap: 0.7rem;
  font-size: 30px;
  color:#b798ab;
}
.icons .delete-icon{
  font-size: 35px;
  transition: all 0.2s;
  cursor: pointer;
}
.icons .delete-icon:hover{
  color:rgb(201, 10, 10);
}
.icons .check-icon{
  transition: all 0.2s;
  cursor: pointer;
  color:rgb(10, 201, 10);

}
.icons .check-icon:hover{
  color:rgb(10, 231, 10);
}
.isEmpty{
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2px;
  padding: 10px 0;
  color:#807579;
  font-weight: 500;
}
.rename-icon.description{
  font-size: 120%;
}
@media (max-width:800px){
  body{
    padding: 1rem 15px;
  }
  .todo-input{
    flex-direction: column;
    width: 100%;
    align-items:flex-start;
    gap: 1.5rem;
  }
  .todo-input-item{;
    gap: 0.6rem;
  }
  .todo-input-item input{
    padding: 0.2rem 0.5rem 0.4rem 0.2rem;
    font-size: 1rem;
    max-width: 100%;
  }
  .todo-input-item input::placeholder{
    font-size: 0.9rem;
    opacity: 0.8;
  }
  .todo-input-item .main-btn{
    font-size: 1rem;
    padding: 0.8rem 2.5rem;
    max-width: 100%;
    width: 100%;
  }
  .todo-input-item.modification{
    width: 100%;
  }
  .btn-area{
    text-align: center;
    padding: 1.1rem 0.3rem 0.5rem 0.3rem;
  }
  .btn-area-items .tabs-btn{
    padding: 0.5rem 2.2rem;
    font-size: 0.9rem;
  }
  .todo-list-item{
    padding: 1rem 1rem;
    gap: 1rem;
  }
  .todo-list-item h3{
    font-size: 18px;
    margin-bottom: 0.3rem;
  }
  .todo-list-item p{
    font-size: 12px;
  }
  .icons{
    gap: 0.4rem;
    font-size: 20px;
  }
  .icons .delete-icon{
    font-size: 25px;
    transition: all 0.2s;
    cursor: pointer;
  }
  .btn-area-items .tabs-btn span{
    padding:1px 2px;
  }
  .todo-list-item p input,
    .todo-list-item h3 input{
    width: 150px;
  }
}